export class ISubmitImpression {
    content: string; // 一句话评论
    tag0: string; // 第1个标签
    tag1: string; // 第2个标签
    tag2: string; // 第3个标签
    tag3: string; // 第4个标签
    tag4: string; // 第5个标签
    tag5: string; // 第6个标签
    uid: string;
    privacy?: number;
    constructor() {
        this['content'] = null;
        this['tag0'] = null;
        this['tag1'] = null;
        this['tag2'] = null;
        this['tag3'] = null;
        this['tag4'] = null;
        this['tag5'] = null;
        this['privacy'] = null;
        this['uid'] = null;
    }
}

export interface IFriendImpression extends ISubmitImpression {
    content: string; // 一句话评论
    tag0: string; // 第1个标签
    tag1: string; // 第2个标签
    tag2: string; // 第3个标签
    tag3: string; // 第4个标签
    tag4: string; // 第5个标签
    tag5: string; // 第6个标签
    uid: string;
    createTime: number; // 创建时间戳 1514546747000
    headPortrait: string; // "HeadPortraits/41321f6bce4745bf93cdd4ce6c5ee394.jpg"
    id: number; // 33132是number???
    privacy: number; // 好友查看权限 0全公开 1仅好友可见 2仅自己可见
    realname: string;
    reviewer: string; // "0f3d0e9f5e0640e6a9bbbcca223a1cd8"
    score: number; // 红娘评价指数，现已无用
    upvoteTimes: number; // 点赞数目
    upvoters: IUpvotersList[]; // 点赞用户列表  
}

export class FriendImpression implements IFriendImpression {
    content: string; // 一句话评论
    tag0: string; // 第1个标签
    tag1: string; // 第2个标签
    tag2: string; // 第3个标签
    tag3: string; // 第4个标签
    tag4: string; // 第5个标签
    tag5: string; // 第6个标签
    uid: string;
    createTime: number; // 创建时间戳 1514546747000
    headPortrait: string; // "HeadPortraits/41321f6bce4745bf93cdd4ce6c5ee394.jpg"
    id: number; // 33132
    privacy: number; // 好友查看权限 0全公开 1仅好友可见 2仅自己可见
    realname: string;
    reviewer: string; // "0f3d0e9f5e0640e6a9bbbcca223a1cd8"
    score: number; // 红娘评价指数，现已无用
    upvoteTimes: number; // 点赞数目
    upvoters: IUpvotersList[]; // 点赞用户列表

    constructor(data: IFriendImpression) {
        for (const key in data) {
            this[key] = data[key];
        }
        this['tagsArray'] = [];
        for (let i = 0; i < 6; i++) {
            if (data['tag' + i] !== null) {
                this['tagsArray'].push(this['tag' + i]);
            }
        }
    }
}

export interface IUpvotersList {
    createTime: string; // 创建时间, 非时间戳 "2017-12-29 07:25:49"
    headPortrait: string;
    osId: string; // 未知，没用到
    uid: string; // 点赞用户id
}
// 统计靠前的好友印象数据
export interface ITopFriendImpression {
    contents: {
        count: number; // 点赞量
        value: string; // 一句话评论
    }[];
    tags: {
        count: number; // 点赞量
        value: string; // 一句话评论
    }[];
}



